<template>
  <div class="user-box">
    <HeadTop title="我的资产" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="top-banner">
      <img v-if="locale == 'en'" src="@/assets/images/userBanner_en.png" alt="">
      <img v-else src="@/assets/images/userBanner.png" alt="">
    </div>
    <div class="user-item">
      <div class="title"> {{$t('index.我的代币资产')}}</div>
      <div class="token-box flex-between">
        <div class="token-item">
        <div class="name">USDT</div>
        <div class="num">{{usdtBalance}}</div>
      </div>
      <div class="token-item">
        <div class="name">FreeDom</div>
        <div class="num">{{freeDomBalance}}</div>
      </div>
      </div>
    </div>

     <div class="user-item">
      <div class="title"> {{$t('index.自由联盟勋章')}}({{nftNum}})</div>
      <div class="mynft-box">
        <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name"> {{$t('index.待领取收益')}}</div>
            <div class="num"> {{$t('index.收益:')}} {{reward}}FreeDom</div>
          </div>
          <div class="get-btn" @click="withdrawReward"> {{$t('index.领取')}}</div>
      </div>
      <div class="mynft-item flex-middle" v-for="(item, index) in list" :key="index">
         <img src="@/assets/images/logo.png" alt="">
          <div class="desc-box flex-item">
            <div class="name">FreeDom #{{item}}</div>
          </div>
      </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import nftAbi from "@/utils/nftAbi";
import { useI18n } from "vue-i18n"

import BigNumber from 'bignumber.js';
import { Toast } from 'vant';


export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      locale: computed(() => store.state.locale),
      freeDomContract: null,
      usdtContract: null,
      nftContract: null,
      freeDomBalance: '0.00',
      usdtBalance: '0.00',
      reward: '0.00',
      list: [],
      nftNum: 0,
      initContract: () => {
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.freeDomContract = new data.web3.eth.Contract(tokenAbi, config.freeDomAddress)
        data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getRewardAmount()
        data.getNft()
      },
       getBalance: async () => {
        let freeDomBalance = await data.freeDomContract.methods.balanceOf(data.address).call()
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** 18).toFixed(2,1)
        data.freeDomBalance =  new BigNumber(freeDomBalance).dividedBy(10 ** 18).toFixed(2,1)
      },
      getNft: async () => {
        let ids = await data.nftContract.methods.getUserAllTokens(data.address).call()
        data.nftNum = ids.length
        data.list = ids
        console.log(ids)
      },
      getRewardAmount: async () => {
        let reward = await data.nftContract.methods.earned(data.address).call()
        data.reward = new BigNumber(reward).dividedBy(10 ** 18).toFixed(3)
      },
      withdrawReward: async () => {
         try {
          await data.nftContract.methods.getReward().send({from: data.address})
          Toast(t('index.领取成功'))
           data.initData()
        } catch (error) {
          Toast(t('index.领取失败'))
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.user-box{
  padding: 20px 16px 15px;
  .top-banner{
    // padding: 16px;
    // background: url('~@/assets/images/item_bg.png') no-repeat;
    // background-size: cover;
    img{
      display: block;
      width: 100%;
    }
    border-radius: 4px;
    margin-bottom: 30px;
    .title{
      font-size: 23px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 5px;
    }
    .desc{
      font-size: 14px;
      color: rgba(255, 255, 255, 0.55);
      margin-bottom: 15px;
    }
    .span-btn{
      height: 22px;
      line-height: 22px;
      font-size: 10px;
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, #FFFFFF 100%);
      color: #F03C85;
      padding: 0 10px;
      border-radius: 12px;
      display: inline-block;
    }
  }
  .user-item{
    margin-bottom: 24px;
    .title{
      font-size: 16px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 14px;
      position: relative;
      padding-left: 10px;
      &::after{
        content: '';
        width: 3px;
        position: absolute;
        height: 100%;
        left: 0;
        background: #66d304;
      }
    }
    .token-box{
      flex-wrap: wrap;
    }
    .token-item{
      padding: 20px;
      color: #fff;
      margin: 5px 0;
      border-radius: 6px;
      width: 49%;
      .name{
        font-size: 16px;
        font-weight: 600;
      }
      .num{
        font-weight: 600;
        font-size: 22px;
      }
      &:nth-child(1) {
        background: url('~@/assets/images/item_1.png') no-repeat;
        background-size: cover;
        .name{
          color: #FFE3C8;
        }
      }
      &:nth-child(2) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
      &:nth-child(3) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
    }
    .nft-box{
      background: linear-gradient(-90deg, #3C3863 0%, #4A3B76 100%);
      border-radius: 4px;
      padding: 0 20px;
      flex-wrap: wrap;
      .nft-item{
        width: 50%;
         padding:20px 15px;
        .name{
          color: #9D8BD0;
          font-size: 14px;
          margin-bottom: 10px;
        }
        .num{
           color: #fff;
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
    .mynft-box{
      .mynft-item{
        background: rgba(255, 255, 255, 0.15);
        padding: 15px;
        border-radius: 4px;
        margin-bottom: 10px;
        img{
          width: 60px;
          height: 60px;
          margin-right: 12px;
          display: block;
        }
        .desc-box{
          .name{
            font-size: 15px;
            color: #fff;
            font-weight: 600;
            margin-bottom: 5px;
          }
          .num{
            font-size: 14px;
            color: #a3ed3e;
          }
        }
        .get-btn{
          background: #66d304;
          height: 30px;
          border-radius: 15px;
          padding: 0 15px;
          color: #fff;
          line-height: 30px;
          font-size: 13px;
        }
      }
    }
  }
}
</style>
